@import "~antd/dist/antd.css";

#root {
  background-color: rgba(210, 210, 210, 0.2);
  height: 100%;
}

@media only screen and (max-width: 600px) {
  .sidebarContainer {
    display: none;
  }
  .showMenu {
    visibility: visible;
  }
  .ant-drawer-body {
    padding: 0;
  }
  .content {
    padding: 16px;
  }
}
@media only screen and (min-width: 600px) {
  .sidebarContainer {
    display: block;
  }
  .showMenu {
    visibility: hidden;
  }
  .content {
    margin: 24px 16px;
    padding: 24px;
  }
}
